@import "ScssHelpers/helpers-all.scss";

.banner_v2 {
	@include bp($point_2, min) {
		padding: 16.7rem var(--gl-indent) 27rem;
	}

	@include bp($point_3) {
		padding: 24.5rem var(--gl-indent) 5.2rem;
	}

	@include bp($point_6 - 1) {
		background-image: linear-gradient(270deg, rgba(63, 86, 17, 1) 0%, rgba(85, 108, 18, 1) 17%, rgba(75, 97, 17, 1) 40%, rgba(34, 53, 12, 1) 100%);
	}
}

.banner_v2__bg_w {
	display: block;
	width: 100%;
	height: 100%;

	@include stretch;

	z-index: 0;

	@include bp($point_6 - 1) {
		opacity: .95;
		max-height: 30.2rem;
	}
}
.banner_v2__bg {
	@include cover_img;

}
.banner_v2__overlay {
	@include bp($point_6 - 1) {
		@include stretch;

		background-image: linear-gradient(7deg, #0a0404 0%, rgba(24, 9, 9, 0.96) 49%, rgba(45, 35, 28, 0.15) 100%);
		opacity: .7;
	}
}
.banner_v2__wrap {
	position: relative;
	z-index: 1;
}

.banner_v2__col {
	@include bp($point_2, min) {
		width: 40.5%;

		margin-left: auto;
	}

	@include bp($point_3, $point_4 - 1) {
		width: 63.5%;
		margin-left: auto;
	}
}

.banner_v2__title {
	@include h2;

	letter-spacing: -0.018em;
// -0.028em
	color: $white;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1.7rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1rem;
		}
	}
}

.banner_v2__text {
	@include text;

	color: $white;

	@include bp($point_4, min) {
		line-height: 1.49;
	}

	@include bp($point_4 - 1) {
		line-height: 1.45;
		letter-spacing: -0.015em;
	}


	&:not(:last-child) {
		
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;

		}

		@include bp($point_3) {
			margin-bottom: 2.3rem;
		}
	}

	p {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 0.9rem;
			}
	
			@include bp($point_3) {
				margin-bottom: 0.7rem;
			}
		}
	}
}
