@import "ScssHelpers/helpers-all.scss";

.banner_v1_bg {
	@include stretch;
}

.banner_v1_bg__in {
	@include cover_img;
}

.banner_v1__wrap {
	position: relative;

	background-color: $white;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, stretch);

		padding: 4.8rem 6.4rem 6.6rem;
	}

	@include bp($point_3) {
		padding: 2rem 2.4rem 3.4rem;
	}
}

.banner_v1__col {
	position: relative;

	@include bp($point_2, min) {
		flex-shrink: 0;
	}

	@include bp($point_3) {
	}

	&:nth-child(2n - 1) {
		@include bp($point_2, min) {
			width: 40%;
			padding-top: 0.6rem;
			padding-right: 3rem;

			&::after {
				content: "";
				position: absolute;
				right: 0;
				top: 0;
				width: 2px;
				height: 100%;

				background-color: $beige_2;
			}
		}

		@include bp($point_3) {
			padding-bottom: 3.4rem;
			&:not(:last-child) {
				margin-bottom: 2.2rem;
			}

			&::after {
				content: "";
				position: absolute;
				right: 0;
				bottom: 0;
				width: 100%;
				height: 1px;

				background-color: $beige_2;
			}
		}
	}

	&:nth-child(2n - 2) {
		@include bp($point_2, min) {
			width: 60%;
			padding-left: 5.8rem;
		}
	}
}

.banner_v1__title {
	@include h2;

	@include bp($point_2, min) {
		letter-spacing: -0.018em;
		line-height: 1;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: -0.026em;
		line-height: 1.08;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.banner_v1__text {
	@include text;

	@include bp($point_2, min) {
		max-width: 38.2rem;

		line-height: 1.5;

		&:not(:last-child) {
			margin-bottom: 2.9rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: -0.018em;
		line-height: 1.42;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.banner_v1__links {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -2rem -2rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.4rem -1.4rem 0;
	}
}

.banner_v1__link {
	@include bp($point_2, min) {
		padding: 0 2rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 1.4rem 1.4rem 0;
	}
}

.banner_v1_list {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -4rem -3.4rem 0;
	}

	@include bp($point_3) {
		margin: 0 -2rem -1.4rem 0;
	}
}

.banner_v1_list__item {
	flex-shrink: 0;

	@include bp($point_2, min) {
		padding: 0 4rem 3.4rem 0;
	}

	@include bp($point_3) {
		padding: 0 2rem 1.4rem 0;
	}

	@include bp($point_7, min) {
		width: 50%;
	}

	@include bp($point_7 - 1) {
		width: 100%;
	}
}

.banner_v1_item__img {
	width: 8.8rem;
	height: 8.8rem;

	img {
		@include cover_img;
	}

	@include bp($point_2, min) {
		width: 8.8rem;
		height: 8.8rem;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_3) {
		width: 7.2rem;
		height: 7.2rem;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.banner_v1_item__title {
	@include h4;

	line-height: 1.15;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	@include bp($point_2, min) {
		letter-spacing: -0.016em;
	}

	@include bp($point_3) {
		letter-spacing: -0.022em;
	}
}

.banner_v1_item__text {
	line-height: 1.5;

	@include bp($point_2, min) {
		@include text;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		line-height: 1.33;
		letter-spacing: -0.016em;
	}
}
